import React, { Component } from "react";
import { Table } from "reactstrap";

// Stats Icon 
import statAntiAir     from "assets/AL/AntiAir.png";
import statArmor       from "assets/AL/Armor.png";
import statASW         from "assets/AL/ASW.png";
import statAviation    from "assets/AL/Aviation.png";
import statConsumption from "assets/AL/Consumption.png";
import statEvasion     from "assets/AL/Evasion.png";
import statFirepower   from "assets/AL/Firepower.png";
import statHealth      from "assets/AL/Health.png";
import statHit         from "assets/AL/Hit.png";
import statLuck        from "assets/AL/Luck.png";
import statReload      from "assets/AL/Reload.png";
import statTorpedo     from "assets/AL/Torpedo.png";

export default class ShipStats extends Component {
  render() {
    const retrofit         = this.props.retrofit;

    // Stats child => (type: string)
    // health, armor, reload, luck, firepower, torpedo, evasion,
    // speed, antiair, aviation, oilConsumption, accuracy, antisubmarineWarfare
    const baseStats        = this.props.baseStats;
    const level100         = this.props.level100;
    const level120         = this.props.level120;
    const level125         = this.props.level125;
    const level100Retrofit = this.props.level100Retrofit;
    const level120Retrofit = this.props.level120Retrofit;


    let statsKai                = null;
    let healthKai               = null;
    let reloadKai               = null;
    let luckKai                 = null;
    let firepowerKai            = null;
    let torpedoKai              = null;
    let evasionKai              = null;
    let speedKai                = null;
    let antiairKai              = null;
    let aviationKai             = null;
    let oilConsumptionKai       = null;
    let accuracyKai             = null;
    let antisubmarineWarfareKai = null;
    let shipArmor               = (<th colSpan="4">{baseStats.armor}</th>);

    if(retrofit) {
      statsKai                = (<><th>100R</th><th>120R</th></>);
      healthKai               = (<><th>{level100Retrofit.health              }</th><th>{level120Retrofit.health              }</th></>);
      reloadKai               = (<><th>{level100Retrofit.reload              }</th><th>{level120Retrofit.reload              }</th></>);
      luckKai                 = (<><th>{level100Retrofit.luck                }</th><th>{level120Retrofit.luck                }</th></>);
      firepowerKai            = (<><th>{level100Retrofit.firepower           }</th><th>{level120Retrofit.firepower           }</th></>);
      torpedoKai              = (<><th>{level100Retrofit.torpedo             }</th><th>{level120Retrofit.torpedo             }</th></>);
      evasionKai              = (<><th>{level100Retrofit.evasion             }</th><th>{level120Retrofit.evasion             }</th></>);
      speedKai                = (<><th>{level100Retrofit.speed               }</th><th>{level120Retrofit.speed               }</th></>);
      antiairKai              = (<><th>{level100Retrofit.antiair             }</th><th>{level120Retrofit.antiair             }</th></>);
      aviationKai             = (<><th>{level100Retrofit.aviation            }</th><th>{level120Retrofit.aviation            }</th></>);
      oilConsumptionKai       = (<><th>{level100Retrofit.oilConsumption      }</th><th>{level120Retrofit.oilConsumption      }</th></>);
      accuracyKai             = (<><th>{level100Retrofit.accuracy            }</th><th>{level120Retrofit.accuracy            }</th></>);
      antisubmarineWarfareKai = (<><th>{level100Retrofit.antisubmarineWarfare}</th><th>{level120Retrofit.antisubmarineWarfare}</th></>);
      if(baseStats.armor === level100Retrofit.armor) {
        shipArmor = (
          <th colSpan="6">{baseStats.armor}</th>
        );
      } else {
        shipArmor = (<><th colSpan="4">{baseStats.armor}</th><th colSpan="2">{level100Retrofit.armor}</th></>);
      }
    }
    return (
      <div>
        <Table className="tableCS">
          <thead>
            <tr>
              <th>Stats</th>
              <th>Base</th>
              <th>100</th>
              <th>120</th>
              <th>125</th>
              {statsKai}
            </tr>
          </thead>
          <tbody>
            <tr>
              <th>
                <img src={statHealth} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
              <th>{baseStats.health}</th>
              <th>{level100.health}</th>
              <th>{level120.health}</th>
              <th>{level125.health}</th>
              {healthKai}
            </tr>
            <tr>
              <th>
                <img src={statArmor} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
              {shipArmor}
            </tr>
            <tr>
              <th>
                <img src={statReload} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
              <th>{baseStats.reload}</th>
              <th>{level100.reload}</th>
              <th>{level120.reload}</th>
              <th>{level125.reload}</th>
              {reloadKai}
            </tr>
            <tr>
              <th>
                <img src={statLuck} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
              <th>{baseStats.luck}</th>
              <th>{level100.luck}</th>
              <th>{level120.luck}</th>
              <th>{level125.luck}</th>
              {luckKai}
            </tr>
            <tr>
              <th>
                <img src={statFirepower} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
              <th>{baseStats.firepower}</th>
              <th>{level100.firepower}</th>
              <th>{level120.firepower}</th>
              <th>{level125.firepower}</th>
              {firepowerKai}
            </tr>
            <tr>
              <th>
                <img src={statTorpedo} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
              <th>{baseStats.torpedo}</th>
              <th>{level100.torpedo}</th>
              <th>{level120.torpedo}</th>
              <th>{level125.torpedo}</th>
              {torpedoKai}
            </tr>
            <tr>
              <th>
                <img src={statEvasion} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
              <th>{baseStats.evasion}</th>
              <th>{level100.evasion}</th>
              <th>{level120.evasion}</th>
              <th>{level125.evasion}</th>
              {evasionKai}
            </tr>
            <tr>
              <th>Spd</th>
              <th>{baseStats.speed}</th>
              <th>{level100.speed}</th>
              <th>{level120.speed}</th>
              <th>{level125.speed}</th>
              {speedKai}
            </tr>
            <tr>
              <th>
                <img src={statAntiAir} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
              <th>{baseStats.antiair}</th>
              <th>{level100.antiair}</th>
              <th>{level120.antiair}</th>
              <th>{level125.antiair}</th>
              {antiairKai}
            </tr>
            <tr>
              <th>
                <img src={statAviation} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
              <th>{baseStats.aviation}</th>
              <th>{level100.aviation}</th>
              <th>{level120.aviation}</th>
              <th>{level125.aviation}</th>
              {aviationKai}
            </tr>
            <tr>
              <th>
                <img src={statConsumption} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
              <th>{baseStats.oilConsumption}</th>
              <th>{level100.oilConsumption}</th>
              <th>{level120.oilConsumption}</th>
              <th>{level125.oilConsumption}</th>
              {oilConsumptionKai}
            </tr>
            <tr>
              <th>
                <img src={statHit} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
              <th>{baseStats.accuracy}</th>
              <th>{level100.accuracy}</th>
              <th>{level120.accuracy}</th>
              <th>{level125.accuracy}</th>
              {accuracyKai}
            </tr>
            <tr>
              <th>
                <img src={statASW} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
              </th>
              <th>{baseStats.antisubmarineWarfare}</th>
              <th>{level100.antisubmarineWarfare}</th>
              <th>{level120.antisubmarineWarfare}</th>
              <th>{level125.antisubmarineWarfare}</th>
              {antisubmarineWarfareKai}
            </tr>
          </tbody>
        </Table>
      </div>
    )
  }
}