import React, { Component } from "react";
import { Redirect } from "react-router-dom";
import ShipProfile from "views/components/Ship/ShipProfile";

export default class ShipFetch extends Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      azurAPI: []
    };
  }

  componentDidMount() {
    this.mounted = true;
    fetch("https://raw.githubusercontent.com/AzurAPI/azurapi-js-setup/master/ships.json")
    .then(res => res.json())
    .then(
      (result) => {
        if(this.mounted) {
            this.setState({
            isLoaded: true,
            azurAPI: result
          });
        }
      },
      (error) => {
        this.setState({
          isLoaded: true,
          error
        });
      }
    )
  }

  componentWillUnmount() {
    this.mounted = false;
  }

  render() {
    const { error, isLoaded, azurAPI } = this.state;
    if (error) {
      return <div>Error: {error.message}</div>;
    } else if (!isLoaded) {
      return <div>Loading...</div>;
    } else {
      const shipName = window.location.href.split("/").slice(-1)[0];
      const shipData = azurAPI.find(ship => ship.names.en.toLowerCase().replace(/\s+/g, "_") === shipName.toLowerCase());
      if(shipData === undefined) {
        return <Redirect to="/ship" />
      } else {
        return <ShipProfile shipData={shipData} /> 
      }
    }
  }
}