import React, { Component } from "react";
import { Table } from "reactstrap";

export default class ShipSlots extends Component {
  render() {
    // Slots child        => {multiple array[]} (type: 4 number, 1 string)
    // Slots[array] child => {maxEfficiency, minEfficiency, type, max, kaiEfficiency} (type: 4 number, 1 string)
    const slots            = this.props.slots;

    return (
      <div>
        <Table className="tableCS shipSlots">
          <thead>
            <tr>
              <th colSpan="3">Equipment Slot</th>
            </tr>
            <tr>
              <th>Equipment</th>
              <th>Efficiency</th>
              <th>Mount</th>
            </tr>
          </thead>
          <tbody>
            {slots.map((slot, key) => {
              const minEfficiency = slot.minEfficiency + "%"
              const maxEfficiency = slot.maxEfficiency + "%"
              let kaiEfficiency = "";
              if(slot.kaiEfficiency) {kaiEfficiency = " → " + slot.kaiEfficiency + "%"}
              const slotType = slot.type;
              const slotEfficiency = minEfficiency + " → " + maxEfficiency + kaiEfficiency;
              const slotMax = slot.max;
              return (
                <tr key={key}>
                  <th>{slotType}</th>
                  <th>{slotEfficiency}</th>
                  <th>{slotMax}</th>
                </tr>
              )
            })}
          </tbody>
        </Table>
      </div>
    )
  }
}